___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --bar-color: #e91e63;
  --bar-size: 0%;
}

.skill-bar {
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 24px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.skill-bar-title {
  text-transform: uppercase;
  display: inline-block;
  vertical-align: middle;
  font-size: 14px !important;
}

.skill-bar-percentage {
  float: right;
  vertical-align: middle;
}

.container-skill-bar {
  background: #000;
  width: 100%;
  height: 5px;
}

.skill-bar-child {
  width: var(--width);
  height: 100%;
  background: var(--bar-color);
  animation: fill-bar 5s ease;
}

@keyframes fill-bar {
  0% {
    width: 0%;
  }
  100% {
    width: var(--width);
  }
}
