___CSS_LOADER_EXPORT___.push([module.id, `.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  margin: 0 5px;
}

.link-nav:hover {
  color: #e91e63;
}
.link-nav {
  color: black;
  padding: 10px;
  transition: color 0.3s;
  font-size: 13px;
}

.active-link {
  color: #ff4081;
  font-weight: bold;
  text-decoration: underline;
}
@media (min-width: 802px) {
  .nav {
    margin: 0 10px;
  }
}

@media (max-width: 768px) {
  .nav {
    margin-bottom: 40px;
  }
  .link-nav {
    color: white;
    padding: 10px;
    transition: color 0.3s;
    font-size: 13px;
  }
}
