  font-family: 'Poppins';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  flex-wrap: wrap;
}

#about-me,
#banner,
#contact,
#skills {
  scroll-margin-top: 100px;
}

body,
html,
.root {
  height: 100vh;
  width: 100vw;
}

html {
  scroll-behavior: smooth;
}
html::-webkit-scrollbar {
  -webkit-appearance: none;
}
html::-webkit-scrollbar:vertical {
  width: 8px;
}
html::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: #e91e63;
  border: 2px solid #e91e63;
}
html::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: rgba(160, 160, 160, 0.68);
}

body {
  font-family: 'Poppins';
  font-size: 15px;
  font-weight: 400;
  color: #232323;
  overflow-x: hidden;
}

ul,
li {
  list-style: none;
  letter-spacing: 1px;
  text-decoration: none;
}

ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

a {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  list-style-type: none;
}

.scroll a {
  color: #232323;
}

h1 {
  font-size: 30px;
  font-weight: 200;
  margin: 0;
}

h2 {
  font-weight: 700;
  font-size: 36px;
  margin-bottom: 5px;
}

h3 {
  font-weight: 600;
  margin-top: 20px;
}

h6 {
  font-weight: 600;
  font-size: 12px;
  margin-top: 10px;
  margin-bottom: 10px;
  line-height: 1.1;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#tab-content {
  width: 100%;
}

.content-resume {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  width: 100%;
}

.nav > a {
  color: #fff;
}

@media (min-width: 768px) {
  .content-resume,
  .container-projects,
  .container-contact,
  .content-banner,
  .container-about,
  .footer {
    min-width: 768px;
  }
}

@media (max-width: 768px) {
  .scroll a {
    color: #fff;
  }
}
