___CSS_LOADER_EXPORT___.push([module.id, `.container-contact {
  margin-top: 60px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}

.content-card-contact {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.contact-me {
  width: 100%;
  max-width: 300px;
  margin: 15px;
  height: auto;
  animation-delay: 0.2s;
  border-radius: 10px;
  text-align: center;
}

.contact-me > a {
  color: #212121;
  text-transform: none;
}

.text-about-h2 {
  font-weight: 700;
  font-size: 36px !important;
  color: #212121;
  line-height: 1;
  text-align: center;
  margin-bottom: 5px;
  margin-top: 20px;
  margin-bottom: 5px;
}

.title-contact {
  font-size: 18px;
}

.icon-social {
  color: #212121;
}

#linkedin {
  width: 100%;
  color: black;
  text-transform: lowercase;
}

@media (max-width: 768px) {
  .content-cards-contact {
    flex-direction: column;
  }
  .contact-me {
    max-width: 95%;
    overflow-x: hidden;
  }
}
